@import url(https://fonts.googleapis.com/css?family=Ubuntu&display);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
/* Constants */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Cribbage specific */
body {
  max-width: 360px;
  font-family: 'Ubuntu', sans-serif;
  color: white;
  background: #6E6650;
  font-size: 1rem;
  margin: 8px; }

button.cribbage-button {
  width: 90px;
  height: 30px;
  padding: 4px;
  margin: 3px;
  color: white;
  background: #4F4A41;
  border: 1px solid #FFFFFF;
  border-radius: 10px;
  font-size: .6rem; }

button.cribbage-button:hover {
  border: #99FFFF 1px solid;
  border-radius: 6px; }

div.cribbage-parent {
  width: 100%; }

div.full-hand-cardsLeft {
  width: 90%; }

div.result-row {
  display: flex;
  justify-content: space-between; }

div.result-image {
  width: 90%; }

div.result-text {
  width: 90%; }

div.show-results {
  display: flex;
  justify-content: left;
  margin: 10px; }

div.CustomHandForm {
  width: 90%;
  margin: 50px 10%; }

div.CustomHandForm > label {
  display: block; }

div#card-options {
  display: flex;
  width: 100%;
  margin: 5px; }

div.cribbage-points > span {
  font-size: .7rem; }

div.card-option {
  padding: 0px;
  width: 54px;
  margin: 4px; }

div.card-option:nth-last-child(1) {
  margin-left: auto; }

div.cribbage-points {
  display: inline-block;
  height: 100%;
  width: 40%;
  margin-left: auto;
  text-align: left;
  vertical-align: middle; }

div.cribbage-points span {
  /*display: inline-block;*/
  font-size: .7rem;
  vertical-align: middle;
  line-height: normal; }

h1.cribbage-text {
  margin: 1.5rem;
  font-size: 1.2rem;
  color: white; }

h2.cribbage-text {
  margin: 1rem;
  color: white; }

h3.cribbage-text {
  color: white;
  font-size: .7rem; }

header.cribbage-header {
  /*border: 1px red solid;*/ }

.hand {
  margin-right: 4px;
  margin-left: 4px; }

img.card {
  height: 77px;
  width: 55px;
  margin: 4px;
  padding: 0px;
  float: left;
  border: 3px solid #6E6659; }

img.c-card {
  height: 77px;
  width: 55px;
  padding: 0px;
  margin: 2px;
  float: right;
  border: 3px solid #6E6659; }

img.result-card {
  height: 42px;
  width: 30px;
  margin-right: 5px;
  margin-bottom: 5px;
  float: left; }

label.cribbage-checkbox {
  font-size: .7rem;
  font-weight: normal; }

p.cribbage-text {
  margin: .9rem;
  font-size: 1rem;
  color: white; }

p.connection-error {
  margin: .9rem;
  padding: 1rem;
  font-size: .9rem;
  color: #EEE;
  background-color: #DD3333; }

section.cribbage-section {
  display: flex;
  align-items: right; }

section.cribbage-results {
  display: flex; }

select.custom-select {
  color: white;
  display: block;
  padding: 2px;
  outline: none;
  background: #4F4A41;
  margin-bottom: .25rem;
  font-size: .6rem;
  max-width: 100%;
  height: 30px;
  line-height: 1; }

select.custom:hover {
  border: 10px solid #99FFFF; }

/* #### Tablets Portrait or Landscape #### */
@media screen and (min-width: 768px) and (max-width: 900px) {
  body {
    max-width: 640px; }
  button.cribbage-button {
    width: 112px;
    height: 30px;
    padding: 6px;
    margin: 7px;
    font-size: .8rem;
    border: 1px solid #FFFFFF;
    border-radius: 8px; }
  button.cribbage-button:hover {
    border-radius: 8px; }
  div.card-option {
    width: 96px; }
  h1.cribbage-text {
    margin: 1rem;
    font-size: 1.5rem; }
  h3.cribbage-text {
    font-size: 1.3rem; }
  img.card {
    height: 135px;
    width: 96px;
    margin: 3px; }
  img.c-card {
    height: 135px;
    width: 96px;
    margin: 4px; }
  img.result-card {
    height: 67px;
    width: 48px;
    margin-right: 6px;
    margin-bottom: 6px; }
  label.cribbage-checkbox {
    font-size: .8rem;
    font-weight: normal; }
  p.cribbage-text {
    margin: 1rem;
    font-size: 1.1rem;
    color: white; }
  select.custom-select {
    width: 96px;
    font-size: .7rem; }
  span.cribbage-points span {
    font-size: .9rem;
    background: red;
    font-size: 10rem; } }

/* #### Desktops #### */
@media screen and (min-width: 901px) {
  body {
    max-width: 820px; }
  button.cribbage-button {
    width: 150px;
    height: 40px;
    padding: 8px;
    margin: 10px;
    font-size: 1rem;
    border: 1px solid #FFFFFF;
    border-radius: 10px; }
  button.cribbage-button:hover {
    border-radius: 10px; }
  div.cribbage-points > span {
    font-size: 1rem; }
  div.card-option {
    width: 128px; }
  h1.cribbage-text {
    margin: .9rem;
    font-size: 2rem; }
  h3.cribbage-text {
    font-size: 1.7rem; }
  img.card {
    height: 180px;
    width: 128px; }
  img.c-card {
    height: 180px;
    width: 128px; }
  img.result-card {
    height: 90px;
    width: 64px;
    margin-right: 2px;
    margin-bottom: 2px; }
  label.cribbage-checkbox {
    font-size: 1rem;
    font-weight: normal; }
  p.cribbage-text {
    margin: 1.5rem;
    font-size: .9rem;
    color: white; }
  select.custom-select {
    width: 128px;
    font-size: .8rem; } }

